import React from 'react'
import data from './data'
import './illust.css'
const IllustContainer = () => {
    return (
        <section>
            <h2 className = "illust__head">What we provide</h2>
            {
                data.map((d,index)=>
                    <div className = 'illust' key = {index}>
                        <div>
                            <h2 className = 'illust__header'>{d.head}</h2>
                            <p className = 'illust__p'>{d.info}</p>
                        </div>
                        <img 
                            className = 'illust__img' 
                            src={d.src} 
                            alt={d.head} 
                        />
                    </div>
                )
            }
        </section>
    )
}

export default IllustContainer
